@tailwind base;
@tailwind components;
@tailwind utilities;


@font-face {
    font-display: swap;
    font-family: 'Rubik';
    font-style: normal;
    font-weight: 300;
    src: url('./Assets/Fonts/rubik-v28-latin-300.woff2') format('woff2'),
        url('./Assets/Fonts/rubik-v28-latin-300.ttf') format('truetype');
}

@font-face {
    font-display: swap;
    font-family: 'Rubik';
    font-style: normal;
    font-weight: 400;
    src: url('./Assets/Fonts/rubik-v28-latin-regular.woff2') format('woff2'),
        url('./Assets/Fonts/rubik-v28-latin-regular.ttf') format('truetype');
}

@font-face {
    font-display: swap;
    font-family: 'Rubik';
    font-style: normal;
    font-weight: 500;
    src: url('./Assets/Fonts/rubik-v28-latin-500.woff2') format('woff2'),
        url('./Assets/Fonts/rubik-v28-latin-500.ttf') format('truetype');
}

body {
    font-family: Rubik, sans-serif;
    background-color: rgb(250, 189, 20);
}
::-webkit-scrollbar{
    display: none;
}
.container {
    width: 1240px;
}